<template>
  <BaseLayout title="发票">
    <template #extra>
      <van-button icon="add" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="onClick">申请开票</van-button>
    </template>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <div v-for="(item, index) in state.list" :key="index">
          <van-tag class="dt" color="#CCC">{{item.dt}}</van-tag>
          <van-cell-group class="item" :border="false">
            <van-cell class="title" :title="'￥ ' + item.value" :style="{color:item.color}">
              <template #value><span class="desc"># {{item.id}}</span></template>
            </van-cell>
            <van-cell class="desc" v-show="item.code.length > 0" :title="item.code">
              <template #value><van-icon name="gold-coin-o" size="12" @click="$copy(item.code)"> 复制票号</van-icon></template>
            </van-cell>
            <van-cell class="desc" :title="item.status != 0 ? item.remark : '正在处理中 ...'" :border="false">
              <template #value><van-icon name="gold-coin-o" size="12" @click="$copy(item.remark)"> 复制备注</van-icon></template>
            </van-cell>
          </van-cell-group>
        </div>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  text-align: center;
}

.item {
  margin: 5px;
  background: #FFF;
  border-radius: 5px;
  text-align: left;
  overflow:hidden;
}
.dt {
  margin: 10px 0 5px 0;
}
.title {
  padding-top: 10px;
  font-size: 16px;
  font-weight: 600;
}
.desc {
  color: #999;
  font-size: 12px;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      grid: true,
      list: [],
      offset: 0,
      limit : 20,
    });
    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/team/invoiceList?offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          item.color = ["#999", "#0C0", "#C00"][item.status];
          this.state.list.push(item);
        }
      });
    },
    onClick() {
      this.$dialog.confirm({message: "确认将剩余未开票金额全部开票？"}).then(() => {
        this.$http.post("/team/invoiceRequest").then(this.onRefresh);
      });
    },
  }
}
</script>
